@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  
  font-family: 'Roboto', sans-serif;
}

.timer-wrapper-desktop{
  display: flex;
  height: 100vh;
  width: 100vw;
  justify-content: space-evenly;
  align-items: center;
  background-color: #e5e5e5;
}
.timer-wrapper-mobile{
    display: flex;
    height: 100vh;
    width: 100vw;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: #e5e5e5;
  }
.timer-logout-desktop {
  position: absolute;
  top: 48px;
  right: 45px;
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
  font-size: 24px;
  background-color: #e5e5e5;
  outline: none;
  border: none;
}
.timer-logout-mobile {
  position: absolute;
  top: 24px;
  right: 23px;
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
  font-size: 18px;
  background-color: #e5e5e5;
  outline: none;
  border: none;
}
.timer-logo-desktop {
  height: 217px;
  width: 183px;
}
.timer-logo-mobile {
  height: 110px;
  width: 93px;
}
.timer-logo-container-desktop {
  border-radius: 25px;
  background-color: #c4c4c4;
  height: 404px;
  width: 379px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.timer-logo-container-mobile {
  height: 208px;
  width: 192px;
  border-radius: 25px;
  background-color: #c4c4c4;
  display: flex;
  justify-content: center;
  align-items: center;
}
.timer-count-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.timer-count-desktop{
    font-weight: 400;
    font-size: 40px;
    margin: 20px;
}
.timer-count-mobile{
    font-weight: 400;
    font-size: 24px;
    margin: 16px;
}
::-webkit-input-placeholder {
  color: black;
}
::-moz-placeholder {
  color: black;
} /* Firefox 19+ */
:-moz-placeholder {
  color: black;
} /* Firefox 18- */
:-ms-input-placeholder {
  color: black;
}

.login-wrapper {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #e5e5e5;
}
.login-form {
    border-radius: 57px;
    background-color: #c4c4c4;
  }
.login-form, .desktop {
  height: 576px;
  width: 588px;
}
.login-form, .mobile {
  height: calc(100vh - 88px);
  margin: 48px 20px 40px 20px;
}
.login-header {
  display: flex;
  justify-content: center;
  margin-top: 110px;
  margin-bottom: 45px;
  font-weight: 500;
  font-size: 46px;
  letter-spacing: 0.75%;
}
.login-input-desktop {
  font-weight: 400;
  font-size: 40px;
  margin: 16px 0px 16px 72px;
  background-color: #c4c4c4;
  outline: none;
  border: none;
  color: black;
}
.login-input-mobile {
  font-weight: 400;
  font-size: 24px;
  margin: 19px 0px 19px 52px;
  background-color: #c4c4c4;
  outline: none;
  border: none;
  color: black;
}
.login-button-wrapper-desktop {
  display: flex;
  justify-content: center;
  margin-top: 64px;
}
.login-button-wrapper-mobile {
  display: flex;
  justify-content: center;
  margin-top: 149px;
}
.login-button-desktop {
  background: #828282;
  border-radius: 63px;
  height: 73px;
  width: 277px;
  outline: none;
  border: none;
  font-weight: 400;
  font-size: 40px;
}
.login-button-mobile {
  background: #828282;
  border-radius: 63px;
  height: 47px;
  width: 195px;
  outline: none;
  border: none;
  font-weight: 400;
  font-size: 24px;
}
.login-redirect {
  display: flex;
  justify-content: center;
  font-family: "Ubuntu", sans-serif;
  font-weight: 400px;
}
.login-redirect,
.desktop {
  margin-top: 19px;
  font-size: 20px;
}
.login-redirect,
.mobile {
  margin-top: 30px;
  font-size: 15px;
}
.login-redirect a {
  color: #88888c;
}
.login-redirect a span {
  color: black;
}

